$gradient-size: 40px;

.actionbar {
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: space-around;

  &--horizontal {
    padding-top: $gradient-size;
    background: rgba(0, 0, 0, 0.6);
    background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.05) 10px,
      rgba(0, 0, 0, 0.4) $gradient-size,
      rgba(0, 0, 0, 0.7)
    );
    right: 0;
  }

  &--vertical {
    padding-right: $gradient-size;
    background: rgba(0, 0, 0, 0.6);
    background: linear-gradient(
      to left,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 0.05) 10px,
      rgba(0, 0, 0, 0.4) $gradient-size,
      rgba(0, 0, 0, 0.7)
    );
    top: 0;
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .actionbar {
    display: none;
  }
}


